import { each } from "./tools";
import { isArray, isPlainObject } from "./types";
/**
 * 获取链接的query
 * @param link
 * @returns
 */
export function getLinkParams(link) {
    var linkType = link.split("?");
    var queryString = linkType[1];
    if (linkType.length > 0 && queryString && queryString !== "") {
        return queryString;
    }
    return "";
}
/**
 * 获取没有参数链接
 * @param link
 * @returns
 */
export function getNotParamsLink(link) {
    if (link === void 0) { link = ""; }
    var linkType = link.split("?");
    return linkType[0];
}
/**
 * query string 转化为 object
 * @param queryString
 * @param decode
 * @returns
 */
export function parseParams(queryString, decode) {
    if (decode === void 0) { decode = true; }
    var linkQuery = {};
    if (!queryString)
        return linkQuery;
    //是否存在原query
    (queryString.split("&") || []).forEach(function (queryItemString) {
        var _a = queryItemString.split("="), key = _a[0], value = _a[1];
        linkQuery[key] = decode ? decodeURIComponent(value) : value;
    });
    return linkQuery;
}
/**
 * query 转化为 string
 * @param query
 * @param encode
 * @returns
 */
export function stringifyParams(query, encode) {
    if (encode === void 0) { encode = true; }
    if (!query)
        return "";
    var _query = [];
    each(query, function (value, key) {
        if (isPlainObject(value) || isArray(value)) {
            value = JSON.stringify(value);
        }
        _query.push(key + "=" + (encode ? encodeURIComponent(value) : value));
    });
    return _query.join("&");
}
