import { hook } from "./tools";
/**
 * 返回promise
 * @param hook
 * @returns
 */
export function promise(hookHandler) {
    var _this = this;
    try {
        return new Promise(function (resolve, reject) {
            hook(_this, hookHandler, [resolve, reject]);
        });
    }
    catch (e) {
        console.warn(e);
    }
}
